<template>
  <div>
    <LayoutBox one="活动报名">
      <el-tabs v-model="activeName" @tab-click="handleClick">
        <el-tab-pane label="所有活动" name="first">
          <!-- 查询页 -->
          <template>
            <div>
              <el-form :model="activeForm" label-width="80px" :inline="true">
                <el-form-item label="类别">
                  <el-select v-model="activeForm.activeType" placeholder="请选择">
                    <el-option label="秒杀" :value="1" />
                    <el-option label="团购" :value="0" />
                  </el-select>
                </el-form-item>
                <el-form-item label="时间">
                  <el-date-picker v-model="activeForm.startTime" type="date" placeholder="开始日期"></el-date-picker>至
                  <el-date-picker v-model="activeForm.endTime" type="date" placeholder="结束日期"></el-date-picker>
                </el-form-item>
                <el-form-item>
                  <el-button type="primary" @click="onActiveSearch">查询</el-button>
                  <el-button type="primary" @click="resetForm('activeForm')">重置</el-button>
                </el-form-item>
              </el-form>
              <!-- 卡片 -->
              <div v-for="(n, i) in activityList" :key="i" class="Ca mb-1 ml-3">
                <div class="Card_from ">
                  <el-card shadow="hover">
                    <div class="d-flex">
                      <div class="img_card mr-1">
                        <el-image :src="n.imageUrl" alt="" />
                      </div>
                      <div class="ml-1 mt-1 ">
                        <div class="fs-md mb-2">{{ n.activeName }}</div>
                        <div class="fs-xs text-grey mb-3 card_text ">
                          {{ n.activeDescribe }}
                        </div>
                        <div class="mt-1 fs-xs">
                          <span
                            class="mr-1"
                          >已报名：<i class="text-primary-1">{{ n.applyNum }}</i></span>
                          <span>活动时间：<i
                            class="text-grey"
                          >{{ n.startTime }}-{{ n.endTime }}</i></span>
                        </div>
                      </div>
                    </div>
                  </el-card>
                  <div v-if="new Date().getTime() >= n.applyStartTimeDate && new Date().getTime() <= n.applyEndTimeDate" class="yuan bg-primary-1" @click="onSignUp(n)">
                    <div class="text-white fs-sm yuan_text">立即报名</div>
                  </div>
                  <div v-else class="yuan bg-primary-1" style="cursor: not-allowed; background-color: #f3d19e;">
                    <div class="text-white fs-sm yuan_text">报名结束</div>
                  </div>
                </div>
              </div>
              <el-pagination
                :current-page="currentPage4"
                :page-sizes="[10, 20, 30, 40]"
                :page-size="allPageSize"
                layout="total, sizes, prev, pager, next, jumper"
                :total="activityListTotal"
                @size-change="handleSizeChange"
                @current-change="handleCurrentChange"
              />
            </div>
          </template>
        </el-tab-pane>
        <el-tab-pane label="我参加的活动" name="second">
          <!-- 我能参加的 -->
          <router-view />
        </el-tab-pane>
        <el-tab-pane label="我收藏的活动" name="fourth">
          <template>
            <div>
              <el-form :model="collectForm" label-width="80px" :inline="true">
                <el-form-item label="类别">
                  <el-select v-model="collectForm.activeType" placeholder="请选择">
                    <el-option label="秒杀" :value="1" />
                    <el-option label="团购" :value="0" />
                  </el-select>
                </el-form-item>
                <el-form-item label="时间">
                  <el-date-picker v-model="collectForm.startTime" type="date" placeholder="开始日期"></el-date-picker>至
                  <el-date-picker v-model="collectForm.endTime" type="date" placeholder="结束日期"></el-date-picker>
                </el-form-item>
                <el-form-item>
                  <el-button type="primary" @click="onCollectSearch">查询</el-button>
                  <el-button type="primary" @click="resetForm('collectForm')">重置</el-button>
                </el-form-item>
              </el-form>
              <!-- 卡片 -->
              <div v-for="(n, i) in activityCollectingList" :key="i" class="Ca mb-1 ml-3">
                <div class="Card_from">
                  <el-card shadow="hover">
                    <div class="d-flex">
                      <div class="img_card mr-1">
                        <el-image :src="n.imageUrl" fit="cover" />
                      </div>
                      <div class="ml-1 mt-1 ">
                        <div class="fs-md mb-2">{{ n.activeName }}</div>
                        <div class="fs-xs text-grey mb-3 card_text ">{{ n.activeDescribe }}</div>
                        <div class="mt-1 fs-xs">
                          <span class="mr-1">已报名：<i class="text-primary-1">{{ n.applyNum }}</i></span>
                          <span>活动时间：<i class="text-grey">{{ n.startTime }} - {{ n.endTime }}</i></span>
                        </div>
                      </div>
                    </div>
                  </el-card>
                  <div v-if="new Date().getTime() >= n.applyStartTimeDate && new Date().getTime() <= n.applyEndTimeDate" class="yuan bg-primary-1" @click="onSignUp(n)">
                    <div class="text-white fs-sm yuan_text">立即报名</div>
                  </div>
                  <div v-else class="yuan bg-primary-1" style="cursor: not-allowed; background-color: #f3d19e;">
                    <div class="text-white fs-sm yuan_text">报名结束</div>
                  </div>
                </div>
              </div>
              <!-- 分页 -->
              <el-pagination
                :current-page="collectingPage"
                :page-sizes="[10, 20, 30, 40]"
                :page-size="CollectingPageSize"
                layout="total, sizes, prev, pager, next, jumper"
                :total="collectingTotal"
                @size-change="handleCollectingSizeChange"
                @current-change="handleCollectingCurrentChange"
              />
            </div>
          </template>
        </el-tab-pane>
      </el-tabs>
    </LayoutBox>
  </div>
</template>

<script>
import LayoutBox from '@/components/LayoutBox/index'

export default {
  components: {
    LayoutBox
  },

  data() {
    return {
      value1: '',
      activeName: 'first',
      value: '',
      collectingPage: 1, // 我收藏的活动的分页现在的页码
      collectingTotal: 0, // 我收藏的活动的总条数
      currentPage4: 1, // 所有活动的分页现在的页码
      activityList: '', // 活动列表
      activityListTotal: 0, // 总条数
      allPageSize: 10, // 所有活动的每页显示多少条
      activeForm: {}, // 所有活动搜索栏表单
      CollectingPageSize: 10, // 我收藏的活动的每页显示多少条
      activityCollectingList: [], // 我收藏的活动列表
      collectForm: {}, // 我的收藏搜索栏表单
    }
  },

  created() {
    this.apiGetActivityList()
    this.apiGetActivityCollectingList()
  },

  methods: {
    handleClick(tab, event) {
      console.log('点击导航栏', tab, event)
    },
    // 点所有活动每页显示多少条的时候
    handleSizeChange(val) {
      this.allPageSize = val
      this.apiGetActivityList()
    },
    // 点所有活动需要跳到第几个页面的时候
    handleCurrentChange(val) {
      this.currentPage4 = val
      this.apiGetActivityList()
    },
    // 点击所有活动的查询
    onActiveSearch () {
      this.apiGetActivityList()
    },
    // 点击收藏查询
    onCollectSearch () {
      this.apiGetActivityCollectingList()
    },
    // 重置查询表单
    resetForm(formName) {
      this.$refs[formName].resetFields()
    },
    // 点我收藏的活动每页显示多少条的时候
    handleCollectingSizeChange(val) {
      this.allPageSize = val
      this.apiGetActivityCollectingList()
    },
    // 点我收藏的活动需要跳到第几个页面的时候
    handleCollectingCurrentChange(val) {
      this.collectingPage = val
      this.apiGetActivityCollectingList()
    },
    // 点立即报名的时候
    onSignUp(n) {
      console.log(n)
      this.$router.push({ path: '/mark/plat/activity/platActivityData', query: { id: n.id }})
    },

    // api 开始----------------------------------------------
    // 查询所有活动列表
    apiGetActivityList() {
      let data = {
        pageNo: this.currentPage4,
        pageSize: this.allPageSize,
        type: 0, // 0-平台 1-商家
      }
      Object.assign(data, this.activeForm) // 模糊查询
      this.$http.post('/woho/activity/manage/query', data)
        .then((res) => {
          this.activityList = res.data.data.list
          this.activityListTotal = res.data.data.total
          // 把时间转换成时间戳再在对象里面添加进去
          this.activityList.map((item) => {
            item.applyStartTimeDate = new Date(item.applyStartTime.replace(/-/g, '/')).getTime()
            item.applyEndTimeDate = new Date(item.applyEndTime.replace(/-/g, '/')).getTime()
          })
        })
    },
    // 收藏活动列表
    apiGetActivityCollectingList() {
      let data = {
        pageNo: this.collectingPage,
        pageSize: this.CollectingPageSize
      }
      Object.assign(data, this.collectForm) // 模糊查询
      this.$http.post('/woho/activity/collecting/queryList', data)
        .then((res) => {
          if (res.data.success) {
            this.activityCollectingList = res.data.data.list
            this.collectingTotal = res.data.data.total
            // 把时间转换成时间戳再在对象里面添加进去
            this.activityCollectingList.map((item) => {
              try {
                item.applyStartTimeDate = new Date(item.applyStartTime.replace(/-/g, '/')).getTime()
                item.applyEndTimeDate = new Date(item.applyEndTime.replace(/-/g, '/')).getTime()
              } catch (error) { console.error('因为收藏夹是空的抛出的异常', error) }
            })
          } else {
            this.$message.error(res.data.msg)
          }
        })
    }

    // api 结束----------------------------------------------
  }
}
</script>

<style lang="scss" scoped>
.Ca {
  position: relative;
}
.Card_from {
  position: relative;
  width: 943px;
  .img_card {
    width: 300px;
    height: 157px;
  }
  .card_text {
    width: 480px;
    line-height: 20px;
  }
}
.yuan {
  position: relative;
  position: absolute;
  top: 10%;
  right: -5%;
  width: 120px;
  height: 120px;
  border-radius: 50%;
  cursor: pointer;
  .yuan_text {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }
}
</style>
